@import url(https://fonts.googleapis.com/css?family=Raleway:400,300,200,500,600,700,800);
@import url(https://fonts.googleapis.com/css?family=Montserrat:400,700);

$contrast: white;
$base: #10262f;

@mixin transition($element, $duration){
  -webkit-transition: $element $duration ease-out;
  -moz-transition: $element $duration ease-out;
  -ms-transition: $element $duration ease-out;
  -o-transition: $element $duration ease-out;
  transition: $element $duration ease-out;
}

body{
  background-color: $base;
  font-family: 'Montserat', sans-serif;
  color: $contrast;
}

.img-center{
  display: block;
  margin: 0 auto;
}